import React from 'react'

import { showAnimation } from '../assets/helper/animations'
import { MenuIcon } from '../assets/svg/svg'

const Header = ({ setNavbar }) => {
	return (
		<div className='header flex items-center justify-between my-10'>
			<MenuIcon className='nav-open w-6 h-6 cursor-pointer' onClick={() => showAnimation(setNavbar)} />
			<nav className='space-x-12 text-sm hidden md:block'>
				<a href='#' className='hover:text-rose-500'>
					Home
				</a>
				<a href='#' className='hover:text-rose-500'>
					Our team
				</a>
				<a href='#' className='hover:text-rose-500'>
					Support
				</a>
				<a href='#' className='bg-rose-500 text-white px-5 py-2 transition-all hover:bg-rose-400'>
					Join Our Discord
				</a>
			</nav>
		</div>
	)
}

export default Header
