import gsap from 'gsap'

export const hideAniamtion = (setNavbar) => {
	const closeNav = () => {
		setNavbar(false)
	}

	const timeline = gsap.timeline({ onComplete: closeNav })
	timeline.to('.Navbar', { x: '-100%', duration: 0.5 })
}

export const brandAnim = (className) => {
	gsap.fromTo(className, { y: 100 }, { y: 0, duration: 1 })
}

export const showAnimation = async (setNavbar) => {
	await setNavbar(true)
	const timeline = gsap.timeline()
	timeline.to('.Navbar', { x: '0%', duration: 0.5 })
}

