const seconds = 0.6

export const darkModeOn = () => {
	gsap.to('body', { backgroundColor: '#000000', duration: seconds })
	gsap.to('.header', { color: '#fff', duration: seconds })
	gsap.to('.nav-open line', { stroke: '#fff', duration: seconds })
	gsap.to('.title ', { color: '#fff', duration: seconds })
	gsap.to('.secondary_text', { color: 'rgb(107 114 128)', duration: seconds })
	gsap.to('.card', { backgroundColor: '#000', duration: seconds })
}

export const darkModeOff = () => {
	gsap.to('body', { backgroundColor: '#fff', duration: seconds })
	gsap.to('.header', { color: '#000', duration: seconds })
	gsap.to('.nav-open line', { stroke: '#000', duration: seconds })
	gsap.to('.title ', { color: '#000', duration: seconds })
	gsap.to('.secondary_text', { color: 'rgb(107 114 128)', duration: seconds })
	gsap.to('.card', { backgroundColor: '#fff', duration: seconds })
}
