import React, { useState } from 'react'
import Header from './components/Header'
import Main from './components/Main'
import Navbar from './components/Navbar'

import mainImg from './assets/svg/allcharachetrs.png'
import logo from './assets/svg/logo.png'
import instImg from './assets/svg/zeninback.svg'
import second from './assets/svg/2.png'
import third from './assets/svg/1.png'
import InstEffect from './components/InstEffect'

const App = () => {
	const [navbar, setNavbar] = useState(true)

	const images = [
		{ id: 0, src: third },
		{ id: 1, src: second },
		{ id: 2, src: third },
	]

	return (
		<div className='container mx-auto px-5 sm:px-0'>
			{navbar && <Navbar setNavbar={setNavbar} />}
			<Header setNavbar={setNavbar} />
			<Main logo={logo} mainImg={mainImg} images={images} />
			<InstEffect img={instImg} />
		</div>
	)
}

export default App
