import React from 'react'

const Main = ({ images, mainImg, logo }) => {
	return (
		<div className='flex flex-col-reverse relative mt-28 md:mb-28 md:flex-row md:pb-72'>
			<div className='flex-1 my-16 text-center space-y-6 md:my-0 md:text-left'>
				<div className='flex justify-center items-center space-x-3 md:justify-start'>
					<h1 className='title text-7xl text-black tracking-wider'>Zenin</h1>
					<img className='w-12 h-12 mt-5 rounded-full ring-white' src={logo} />
				</div>
				<h2 className='text-rose-500 text-xl tracking-[0.3rem] font-mono'>
					Dont follow the meta, become the meta.
				</h2>
				<p className='text-gray-500 text-sm font-mono'>
					You can get zenlist by engaging in our chat channels and sharing time with the community. - We value quality
					over quantity, if you spam or ask for zenlist there is zero chance you get it. You can post your fan-art,
					emojis and stickers for Zenin on #fan-art. The best ones might be rewarded with zenlist
				</p>

				<div className='flex flex-col space-y-12 justify-center items-center space-x-0 py-10 sm:flex-row sm:space-x-6 sm:space-y-0 sm:justify-start sm:items-start md:absolute md:transition-all md:duration-300 md:-translate-x-96 md:hover:-translate-x-32 md:xl:-translate-x-52 md:xl:hover:-translate-x-0'>
					{images.map(({ id, src }) => (
						<div
							key={id}
							className='title card flex max-w-[250px] flex-col items-center justify-center overflow-hidden rounded border border-gray-200 cursor-pointer shadow-xl'
						>
							<img className='w-full h-full' src={src} />
						</div>
					))}
				</div>
			</div>

			<div className='flex-1 flex justify-center md:justify-end xl:justify-center'>
				<div className='w-[70%]'>
					<img className='w-full h-full rounded-full shadow-lg shadow-rose-200' src={mainImg} />
				</div>
			</div>
		</div>
	)
}

export default Main
