import React, { useState, useEffect } from 'react'

import { brandAnim, darkModeOff, darkModeOn, hideAniamtion } from '../assets/helper/animations'
import { CloseIcon } from '../assets/svg/svg'

const Navbar = ({ setNavbar }) => {
	const [check, setCheck] = useState(false)

	useEffect(() => brandAnim('.logoBrand'), [])

	return (
		<div className='fixed Navbar top-0 left-0 -translate-x-full h-screen w-[300px] bg-black z-50 p-7'>
			<CloseIcon className='w-6 h-6 ml-auto cursor-pointer' onClick={() => hideAniamtion(setNavbar)} />

			<div className='text-white flex my-14 mx-5 flex-col space-y-3 text-3xl tracking-wider'>
				<a
					href='#'
					className='underline-anim cursor-pointer'
					onMouseEnter={(e) => e.target.classList.add('active')}
					onMouseLeave={(e) => e.target.classList.remove('active')}
				>
					Home
				</a>
				<a
					href='#'
					className='underline-anim cursor-pointer'
					onMouseEnter={(e) => e.target.classList.add('active')}
					onMouseLeave={(e) => e.target.classList.remove('active')}
				>
					Our team
				</a>
				<a
					href='#'
					className='underline-anim cursor-pointer'
					onMouseEnter={(e) => e.target.classList.add('active')}
					onMouseLeave={(e) => e.target.classList.remove('active')}
				>
					Support
				</a>
				<a
					href='#'
					className='underline-anim cursor-pointer'
					onMouseEnter={(e) => e.target.classList.add('active')}
					onMouseLeave={(e) => e.target.classList.remove('active')}
				>
					Join Discord
				</a>

				<label className='switch'>
					<input
						type='checkbox'
						checked={check}
						onChange={(e) => {
							e.target.checked ? darkModeOn() : darkModeOff()
							setCheck(e.target.checked)
						}}
					/>
					<span className='slider round'></span>
				</label>

				<div className='logoBrand fixed bottom-7 right-7 translate-y-full'>Zenin</div>
			</div>
		</div>
	)
}

export default Navbar
