import React from 'react'

const InstEffect = ({ img }) => {
	return (
		<div className='flex flex-col justify-center items-center text-center space-y-12 my-32'>
			<div className='space-y-2'>
				<h2 className='title text-3xl'>Scan image to check magic</h2>
				<p className='text-lg text-gray-500 space-x-3'>
					<span className='title'>Link to magic effect instagram!</span>
					<a
						href='https://www.instagram.com/ar/1061820891099757/?ch=NmRmMWI0M2M4ZmFlZDhjNmI1NjU0MzIwOWNiMTJhOTU%3D'
						className='underline text-rose-500'
					>
						here
					</a>
				</p>
			</div>

			<img src={img} />
		</div>
	)
}

export default InstEffect
